import { Component, Input, EventEmitter, Output } from '@angular/core';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

/**
 * Importa el componente de Checks con los diseños de Bancolombia S.A.
 *
 * @export
 * @class BcButtonGroupComponent
 * @implements {OnInit}
 */
@Component({
  selector: 'bc-button-group',
  templateUrl: 'bc-button-group.component.html'
})

export class BcButtonGroupComponent {
  parentReady: boolean = false;
  /**
   * Id del elemento.
   *
   * @memberof BcButtonGroupComponent
   */
  @Input() buttonId = this.helper.getId(this);
  /**
   * valor del elemento.
   *
   * @memberof BcButtonGroupComponent
   */
  @Input() value = '';
  /**
   * Name del elemento.
   *
   * @memberof BcButtonGroupComponent
   */
  @Input() name = '';
  /**
   * taxindex del elemento.
   *
   * @memberof BcButtonGroupComponent
   */

  @Input() tabindex = 0;
  /**
   * Muestra opción seleccionada.
   *
   * @memberof BcButtonGroupComponent
   */
  @Output() changeState = new EventEmitter<any>();

  constructor(private helper: BcHelperService) { }
}
